package dr.ulysses.database

import app.cash.sqldelight.Transacter
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import dr.ulysses.database.composeApp.newInstance
import dr.ulysses.database.composeApp.schema
import kotlin.Unit

public interface Database : Transacter {
  public val settingQueries: SettingQueries

  public val songQueries: SongQueries

  public companion object {
    public val Schema: SqlSchema<QueryResult.Value<Unit>>
      get() = Database::class.schema

    public operator fun invoke(driver: SqlDriver): Database = Database::class.newInstance(driver)
  }
}
