package dr.ulysses.database

import app.cash.sqldelight.Query
import app.cash.sqldelight.TransacterImpl
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlCursor
import app.cash.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.ByteArray
import kotlin.Long
import kotlin.String

public class SongQueries(
  driver: SqlDriver,
) : TransacterImpl(driver) {
  public fun <T : Any> selectAll(mapper: (
    path: String,
    title: String,
    album: String?,
    artist: String,
    duration: Long?,
    artwork: ByteArray?,
    state: String?,
  ) -> T): Query<T> = Query(1_433_118_721, arrayOf("song"), driver, "Song.sq", "selectAll",
      "SELECT song.path, song.title, song.album, song.artist, song.duration, song.artwork, song.state FROM song") {
      cursor ->
    mapper(
      cursor.getString(0)!!,
      cursor.getString(1)!!,
      cursor.getString(2),
      cursor.getString(3)!!,
      cursor.getLong(4),
      cursor.getBytes(5),
      cursor.getString(6)
    )
  }

  public fun selectAll(): Query<Song> = selectAll { path, title, album, artist, duration, artwork,
      state ->
    Song(
      path,
      title,
      album,
      artist,
      duration,
      artwork,
      state
    )
  }

  public fun <T : Any> selectAllSongs(mapper: (
    path: String,
    title: String,
    artist: String,
    album: String?,
  ) -> T): Query<T> = Query(169_340_541, arrayOf("song"), driver, "Song.sq", "selectAllSongs",
      "SELECT path, title, artist, album FROM song") { cursor ->
    mapper(
      cursor.getString(0)!!,
      cursor.getString(1)!!,
      cursor.getString(2)!!,
      cursor.getString(3)
    )
  }

  public fun selectAllSongs(): Query<SelectAllSongs> = selectAllSongs { path, title, artist,
      album ->
    SelectAllSongs(
      path,
      title,
      artist,
      album
    )
  }

  public fun selectAllArtists(): Query<String> = Query(823_797_451, arrayOf("song"), driver,
      "Song.sq", "selectAllArtists", "SELECT DISTINCT artist FROM song") { cursor ->
    cursor.getString(0)!!
  }

  public fun <T : Any> selectAllAlbums(mapper: (album: String?) -> T): Query<T> = Query(436_150_085,
      arrayOf("song"), driver, "Song.sq", "selectAllAlbums", "SELECT DISTINCT album FROM song") {
      cursor ->
    mapper(
      cursor.getString(0)
    )
  }

  public fun selectAllAlbums(): Query<SelectAllAlbums> = selectAllAlbums { album ->
    SelectAllAlbums(
      album
    )
  }

  public fun <T : Any> selectArtworkByPath(path: String, mapper: (artwork: ByteArray?) -> T):
      Query<T> = SelectArtworkByPathQuery(path) { cursor ->
    mapper(
      cursor.getBytes(0)
    )
  }

  public fun selectArtworkByPath(path: String): Query<SelectArtworkByPath> =
      selectArtworkByPath(path) { artwork ->
    SelectArtworkByPath(
      artwork
    )
  }

  public fun insert(
    title: String,
    album: String?,
    artist: String,
    path: String,
    duration: Long?,
    state: String?,
    artwork: ByteArray?,
  ) {
    driver.execute(971_798_333,
        """INSERT INTO song(title, album, artist, path, duration, state, artwork) VALUES(?, ?, ?, ?, ?, ?, ?)""",
        7) {
          bindString(0, title)
          bindString(1, album)
          bindString(2, artist)
          bindString(3, path)
          bindLong(4, duration)
          bindString(5, state)
          bindBytes(6, artwork)
        }
    notifyQueries(971_798_333) { emit ->
      emit("song")
    }
  }

  public fun update(
    title: String,
    album: String?,
    artist: String,
    duration: Long?,
    state: String?,
    artwork: ByteArray?,
    path: String,
  ) {
    driver.execute(1_316_744_525,
        """UPDATE song SET title = ?, album = ?, artist = ?, duration = ?, state = ?, artwork = ? WHERE path = ?""",
        7) {
          bindString(0, title)
          bindString(1, album)
          bindString(2, artist)
          bindLong(3, duration)
          bindString(4, state)
          bindBytes(5, artwork)
          bindString(6, path)
        }
    notifyQueries(1_316_744_525) { emit ->
      emit("song")
    }
  }

  public fun deleteByPath(path: String) {
    driver.execute(-696_096_725, """DELETE FROM song WHERE path = ?""", 1) {
          bindString(0, path)
        }
    notifyQueries(-696_096_725) { emit ->
      emit("song")
    }
  }

  private inner class SelectArtworkByPathQuery<out T : Any>(
    public val path: String,
    mapper: (SqlCursor) -> T,
  ) : Query<T>(mapper) {
    override fun addListener(listener: Query.Listener) {
      driver.addListener("song", listener = listener)
    }

    override fun removeListener(listener: Query.Listener) {
      driver.removeListener("song", listener = listener)
    }

    override fun <R> execute(mapper: (SqlCursor) -> QueryResult<R>): QueryResult<R> =
        driver.executeQuery(-118_680_432, """SELECT artwork FROM song WHERE path = ?""", mapper, 1)
        {
      bindString(0, path)
    }

    override fun toString(): String = "Song.sq:selectArtworkByPath"
  }
}
