  public fun updateByKey(value_: String?, key: String) {
    driver.execute(-712_150_014, """UPDATE setting SET value = ? WHERE key = ?""", 2) {
          bindString(0, value_)
          bindString(1, key)
        }
    notifyQueries(-712_150_014) { emit ->
